
.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
}

.content {
  width: 100vw;
}


nav.navbar {
  margin-bottom: 1em;
}

img.menu-icon {
  width: 1.5em;
  margin: .3em 1em .3em .3em;
  cursor: pointer;
}

:root {
    --sidebarWidth: 300px;
    --sidebarBackground: #404E67;
    --sidebarActiveLink: #F8877C;
    --sidebarLink: #FDFDFE;
}


#sidebar {
    width: 300px;
    width: var(--sidebarWidth);
    height: 100vh;


    top: 0;
    left: 0;
    z-index: 999;
    margin-left: calc(-1 * 300px);
    margin-left: calc(-1 * var(--sidebarWidth));

    background: #404E67;

    background: var(--sidebarBackground);
    color: #fff;
    transition: all 0.3s;
}

#sidebar h2.app-title {
    padding: .5em;
}

#sidebar.open {
    margin-left: 0;
}

li.sidebar-link {
    color: #FDFDFE;
    color: var(--sidebarLink);
    cursor: pointer;
    background-color: transparent;
    background-color: initial;
}

li.sidebar-link a.active {
    color: #F8877C;
    color: var(--sidebarActiveLink);
    background-color: transparent;
    background-color: initial;
    border: none;
}


a.icon-button {
    margin-right: .5em;
}



.form-buttons button.submit-project-form {
    max-height: 38px;
}

div.project-form-container {
    margin-bottom: 1em;
}



a.back-button {
    margin-bottom: 1em;
}

div.modal-footer .btn-toolbar {
    width: 100%;
}

.table.items-table td,
.table.items-table th {
    padding: 8px;
}

button.button-link {
  border: none;
  background-color: transparent;
}

/* Tables */
td > a,
td > a:hover {
  color: black;
}

/* Forms */
.form-buttons .btn:first-child{
  margin-right: auto;
}

